* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --primary-match: #59a7a6;
    --secondary-match: #204060;
}

header {
    height: 100px;
    background: #262626;
    background-size: cover;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    top: 0;
}

body {
    background-color: #edf2f8;
    position: relative;
    min-height: 100vh;
}

header h1 {
    margin: 10px 0 10px 0;
    font-size: 50px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    color: black;
    background-color: var(--primary-match);
    padding: 10px 45px;
}

header h1 a {
    text-decoration: none;
    color: black;
}

header h1 a:hover {
    color: #262626;
}

.nav {
    margin: 30px;
    display: flex;
    flex-wrap: wrap;
    letter-spacing: 3px;
    text-decoration: none;
}

.nav ul {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
}

.nav ul li a {
    color: var(--primary-match);
    padding: 10px;
    text-decoration: underline 3px;
    text-underline-offset: 10px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 20px;
}

.nav ul li a:hover {
    color: aqua;
}

.title-image {
    background-image: url("./images/title-image.jpg");
    background-attachment: fixed;
    height: 300px;
    width: 100%;
    background-position: center;
    background-size: cover;
}

.subtitle {
    text-align: right;
    float: right;
    margin-top: 100px;
    margin-right: 50px;
    font-size: 40px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    background-color: var(--primary-match);
    padding: 10px;
    opacity: 0.9;
}

.subtitle:hover {
    opacity: 1;
}

section {
    margin-bottom: 30px;
}

.section-titles {
    width: 220px;
}

.section-titles h3{
    color: var(--secondary-match);
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 50px;
    border-right: solid 3px var(--secondary-match);
    text-align: left;
}

.items {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    padding: 20px;
}

.project-sites {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 800px;
}

.project-sites img {
    display: flex;
    float: right;
    border: solid 7px var(--primary-match);
    margin: 10px;
    opacity: 0.8;
    flex: 1;
}

.project-sites img:hover {
    opacity: 1;
}

.image-1 {
    position: relative;
}

.image-1 img {
    display: flex;
    height: 450px;
    width: 800px;
    overflow: hidden;
}

.image-1 p {
    position: absolute;
    top: 300px;
    left: 10px;
    font-size: 20px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    padding: 10px 10px 10px 50px;
    background-color: var(--primary-match);
    color: var(--secondary-match);
    font-weight: 800;
}

.image-2 {
    display: flex;
    position: relative;
}

.image-2 img {
    height: 200px;
    width: 380px;
    overflow: hidden;
}

.image-2 p {
    position: absolute;
    top: 150px;
    left: 10px;
    font-size: 20px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    padding: 10px 10px 10px 50px;
    background-color: var(--primary-match);
    color: var(--secondary-match);
    font-weight: 800;
}

.info {
    width: 800px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    color: black;
}

.info p {
    font-size: 16px;
    line-height: 1.25;
    margin-bottom: 10px;
}

.find-me {
    display: table-row;
    justify-content: space-between;
    width: 800px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    color: black;
}

.find-me ul li a:hover {
    color: var(--primary-match);
}

#contact-form {
    line-height: 2;
    width: 300px;
}

#contact-form input, textarea {
    width: 100%;
}


footer {
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    background: #292929;
    color: #ffffff;
    bottom: 0;
    width: 100%;
    height: 75px;
    position: absolute;
    text-decoration: underline;
    font-size: 24px;
  }

  footer a {
    color: white;
    text-decoration: none;
    padding-left: 15px;
    padding-right: 15px;
  }

@media screen and (max-width:980px) {
    header {
        justify-content: center;
        position: relative;
    }

    header h1 {
        width: 100%;
        text-align: center;
    }

    section {
        margin-bottom: 20px;
    }

    .section-titles {
        margin-bottom: 20px;
    }

    .nav ul {
        background-color: #262626;
        background-size: cover;
        width: 100%;
        justify-content: center;
        padding: 20px;
    }

    .nav ul li a {
        font-size: 20px;
    }

    .title-image {
        width: 100%;
    }

    .subtitle {
        justify-content: center;
        font-size: 20px;
        margin-top: 150px;
    }

    .project-sites img {
        max-width: 100%;
        max-height: 100%;
    }
}